import React ,{useState} from 'react'


export default function Textform() {

    const [text , settext] = useState("")

       //functions
       const handle = (e) => {
        settext(e.target.value);
        }
  
      const Clear = () => {
        let newtext="";
        settext(newtext);
      }
  
      const UpperCase = () => {
          let newtext = text.toUpperCase();
          settext(newtext);
        }
  
        const LowerCase = () => {
          let newtext= text.toLowerCase();
          settext(newtext);
        }
  
        const Copy = () => {
          navigator.clipboard.writeText(text).then(()=> {
              alert("Copied..");
          })
        }
  
        const Removextra = () => {
            let newtext = text.trim().replace(/\s+/g,' ')
            settext(newtext);
          }

         
  return (
    <>

    <div>
    <h2 className=' p-3'>Text Editor....!!!!</h2>

    <div className=" p-4 mb-2">
        <textarea className="form-control" value={text} onChange={handle} id="mybox" rows="8" placeholder='Enter...'></textarea>
    </div>

<div className=' d-flex '>
    <button className="btn btn-outline-success m-2" onClick={Copy}>Copy</button>
    <button className="btn btn-outline-success m-2" onClick={Clear}>Clear</button>
    <button className="btn btn-outline-success m-2" onClick={UpperCase}>UpperCase</button>
    <button className="btn btn-outline-success m-2" onClick={LowerCase}>LowerCase</button>
    <button className="btn btn-outline-success m-2" onClick={Removextra}>Remove Extra Spaces</button>
</div>
<div className='mb-3 p-4'>
<h2> Preview </h2>
<p>{text}</p>
</div>
</div>

    </>
  )
}
